import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router, ROUTES } from '@angular/router';
import { AppStateService } from '../app.state.service';
import { BasePageComponent } from '../base-page.component';

declare var ng: any;

@Component({
  selector: 'app-aboutme',
  templateUrl: './aboutme.component.html',
  styleUrls: ['./aboutme.component.scss'],
  preserveWhitespaces: true,
  encapsulation: ViewEncapsulation.None

})
export class AboutmeComponent extends BasePageComponent implements OnInit {
  ngOnInit() { }
  year: number;
  old: number;
  constructor(private route: ActivatedRoute, router: Router, baseState: AppStateService) {
    super(router, baseState);
    this.year = new Date().getFullYear() - new Date(2018, 7).getFullYear();
    this.old = new Date().getFullYear() - new Date(1995, 10, 25).getFullYear();
  }

  openToLink(url: string) {
    window.open(url);
  }
}
